@import "/src/scss/variables.scss";

.header {
	color: $secondColor;
	width: 100%;
	height: 5rem;
	background-color: #5a0b0d;

	&__nav {
		background-color: inherit;
		width: 100%;
		max-width: 1300px;
		height: inherit;
		display: flex;
		align-items: center;
		position: fixed;
		left: 50%;
		top: 0%;
		transform: translate(-50%, 0);
		z-index: 1000000;
	}

	&__logo {
		width: 25%;
		text-align: center;
		cursor: pointer;
		max-height: 100%;
		height: 100%;

		& img {
			object-fit: cover;
			max-width: 100%;
			border-radius: 40%;
			max-height: 100%;
		}
	}

	&__ul {
		width: 70%;
		display: flex;
		justify-content: flex-end;
		align-items: center;
	}

	.nav-item {
		padding: 0 15px;
		text-transform: capitalize;
	}
}
